.masters {
    padding-top: 120px;
    padding-bottom: 150px;
    ul {
        list-style-type: disc;
        margin-block-start: 1em;
        margin-block-end: 1em;
        margin-inline-start: 0px;
        margin-inline-end: 0px;
        padding-inline-start: 40px;
        unicode-bidi: isolate;
    }
}

.masters_container {
    display: flex;
    flex-wrap: wrap;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    max-width: 1200px;
    box-sizing: content-box;
    list-style-type: disc;
    padding-bottom: 3em;
}

.masters_items {
    margin-top: 2em;
    display: inline;
    float: left;
    margin-left: 20px;
    margin-right: 20px;
    width: 100%;
    max-width: 560px;
}

.masters_img {
    margin-bottom: 30px;
    width: 100%;
    max-width: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

.masters_name{
    font-size: 20px;
    font-weight: 750;
    padding-bottom: 20px;
}

.masters_strip {
    width: 100px;   /* Длина полоски */
    height: 2px;    /* Толщина полоски */
    background: lightgray;
}

.masters_desc {
    padding-top: 15px;
    padding-bottom: 15px;
}